.notice {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  width: 280px;


  &__img {
    width: 120px;
    height: 120px;
    object-fit: contain;
    margin-bottom: 20px;

    @media screen and (max-width: 600px) {
      & {
        width: 100px;
        height: 100px;
      }
    }
  }

  &__title {
    margin: 0 0 10px;
    padding: 0;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    color: #000000;

    @media screen and (max-width: 600px) {
      & {
        font-size: 18px;
      }
    }
  }

  &__text {
    margin: 0;
    padding: 0;
    text-align: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
    opacity: 0.4;

    @media screen and (max-width: 600px) {
      & {
        font-size: 12px;
      }
    }
  }

  &__btn {
    margin: 40px 0 0;
    padding: 0;
    border: none;
    width: 280px;
    padding: 20px 30px;
    background: #9DD558;
    border-radius: 18px;
    display: grid;
    grid-template-columns: 14px 1fr 14px;
    align-items: center;
    transition: opacity .2s;

    &:hover {
      cursor: pointer;
      opacity: .6;
    }
  }

  &__btn-text {
    grid-column: 2/3;
    margin: 0;
    padding: 0;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #FFFFFF;

    @media screen and (max-width: 600px) {
      & {
        font-size: 16px;
      }
    }
  }

  &__btn-arrow {
    width: 14px;
    height: 12px;
    background: url(../../../public/images/btn-arrow.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    transform: rotate(-180deg)
  }
}
